<template>
    <div class="container-fluid col-10">
        <table class="table" id="vacancys">
            <thead>
            <tr>
                <th scope="col"></th>
                <th scope="col">ID</th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col">email</th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
                <th scope="col"></th>
            </tr>
            </thead>
        <tbody> 
        <tr v-for="v in VacansysList" :key="v.id">
            <td>
                <button onclick="return confirm(' ?')" @click="deleteOne(v.id)">
                    <span class="btn btn-danger me-2"></span>
                </button>
            </td>
            <td>{{v.id}}</td>
            <td>{{v.company}}</td>
            <td>{{v.post}}</td>
            <td>{{v.decripton}}</td>
            <td>{{v.e_mail}}</td>
            <td>{{v.phone_number}}</td>
            <td>{{v.get_priority}}</td>
            <td>{{v.hr_manager}}</td>
            <td>{{v.file}}</td>
        </tr>
        </tbody>
    </table>

</div>
</template>

<script>
import axios from 'axios'
export default {
    name: "v-vacancy-table",
    props:['modelValue'],
data(){
    return {
    VacansysList: []
  }
},
mounted(){
    this.getVacancyList();
},
methods: {
    async getVacancyList(){
        let params={}
        let data = (await axios.get("http://localhost:8000/api/vacancys/",{params})).data
        this.VacansysList = data
        },


    deleteOne(id){
        axios.delete(`http://localhost:8000/api/vacancy-delete/${id}/`)

    },


    editOne(id){
        axios.put(`http://localhost:8000/api/vacancys/${id}`,{id:this.id})
            .then(()=>{
            this.getAll();
            })
    },

    post(){
        if(this.id == 0){
            axios.post(`http://localhost:8000/api/vacancys/`,{id:this.id})
                .then(()=>{
                this.getAll();
                })
        }
        else {
            this.editOne(this.id);
             }
                this.id=0;
                this.title='';
                this.link='';
    },
}
}
</script>
